<!-- 建筑场所 -->
<template>
    <div class="area">
        <base-title title="建筑场所">
            <template slot="filterTree">
                <!--<slelct-tree
                    @on-change="handleChangeSelect"
                    :currentSelect="currentSelect"
                    :treeList="treeList"
                ></slelct-tree>-->
                <slelct-Tree :rootId="rootId" @node-select="orgSelect" style="margin-left: 15px"></slelct-Tree>
            </template>
            <template slot="btn">
                <btn-list @btn-click="handleAdd" :model="{icon:'icon-add',name:'添加'}"></btn-list>
            </template>
        </base-title>

        <div class="area-content">
            <base-table
                :tableData="tableData"
                :tableColumn="tableColumn"
                @on-stop="handleStop"
                @on-edit="handleEdit"
            ></base-table>
        </div>

        <div class="area-footer">
            <state-switch @switchL="handleSwicthState"></state-switch>
            <page :pageTotal="pageTotal" @handleSizeChange="SizeChange" @handleCurrentChange="CurrentChange"></page>
        </div>

        <!-- modal -->
        <div class="build-area-mdoal">
            <el-form
                style="width:330px;margin:24px auto"
                ref="formInfo"
                :model="formInfo"
                label-width="80px"
                :rules="formRules"
            >
                <el-form-item label="机构" prop="orgId">
                    <org-Select :rootId="orgId"  :initText="orgSelectText" :refreshText="refreshText"   @node-select="handleOrgSelect"></org-Select>
                </el-form-item>
                <el-form-item label="名称" prop="name">
                    <el-input v-model="formInfo.name"></el-input>
                </el-form-item>
                <el-form-item label="所属校区" v-show="campusDiv">
                    <el-input
                            @focus="isShowT()"
                            placeholder="输入关键字进行过滤"
                            v-model="formInfo.campusId">
                        <i slot="suffix" @click="isShow(!campusShow)" class="iconfont icon-school"></i>
                    </el-input>
                    <div class="campusId" v-show="campusShow">
                        <el-tree
                                class="filter-tree"
                                :data="campusData"
                                :props="defaultProps"
                                @node-click="handleNodeClick"
                                ref="tree">
                        </el-tree>
                    </div>
                </el-form-item>
                <el-form-item label="描述">
                    <el-input v-model="formInfo.description"></el-input>
                </el-form-item>
                <el-form-item label="显示顺序">
                    <el-input v-model="formInfo.orderIndex"></el-input>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
import {isBoss,isCity,isArea,isSchool,isSchoolArea,isDepartment,} from "../../../assets/javascript/orgType.js"
export default {
    components: {
        stateSwitch: () => import("./../../../components/state-switch"),
        baseTable: () => import("./../../../components/table"),
        slelctTree: () => import("./../../../components/org-menu"),
        orgSelect: () => import("./../../../components/org-select"),
        baseTitle: () => import("@/components/title"),
        btnList: () => import("@/components/btn-list"),
        page: () => import("@/components/paging")
    },
    data() {
        const tableColumn = [
            {
                prop: "orderIndex",
                label: "显示顺序",
            },
            {
                prop: "name",
                label: "名称",
            },
            {
                prop: "orgName",
                label: "所属机构",
            },
            {
                prop: "campusName",
                label: "校区",
            },
            {
                prop: "description",
                label: "描述",
            },
            {
                prop: "statusText",
                label: "状态",
            }
        ];
        return {
            defaultProps: {
                children: 'children',
                label: 'displayName'
            },
            campusShow:false,
            campusDiv:true,
            campusId:'',
            campusData:[],
            //rootId
            rootId:'0',
            tableData: [],
            //分页
            pageSize: 10,//显示多少页
            pageCurrent: 1,//当前页
            pageTotal: 0,//总条数
            tableColumn,
            //
            /* switch status */
            status: 0, //状态:0启动、1停用、全部''
            /* dialog */
            orgSelectText:'',
            refreshText:false,
            orgId:'0',
            formInfo: {
                orgId:'',
                campusId: '', // 校区ID
                name: "", // 名称
                orderIndex: '', // 顺序
                description: "", // 描述
            },
            formRules: {
                orgId: [
                    { required: true, message: "请选择机构", trigger: "blur" }
                ],
                name: [
                    { required: true, message: "请输入名称", trigger: "blur" }
                ],
            }
        };
    },
    created() {
        this.areasList();
    },
    methods: {
        /*机构id*/
        handleOrgSelect(val,callback){
            this.$refs['formInfo'].resetFields();
            this.campusId = '';
            this.formInfo.campusId = '';
            this.orgId = val.id;
            this.formInfo.orgId = val.id;
            this.insSelect();
            callback(true);
        },
        //获取校区下拉框数据
        insSelect(){
            let params = {
                schoolOrgId :this.orgId
            };
            this.$api.insSelect(params).then(res => {
                if (res.success == true) {
                    if(res.data == ''){
                        this.campusDiv = false;
                    }else {
                        this.campusData = res.data;
                        this.campusDiv = true;
                    }
                }
            })
        },
        isShow(){this.campusShow = !this.campusShow;},
        isShowT(){this.campusShow = true;},
        handleNodeClick(val){
            this.campusId = val.id;
            this.formInfo.campusId = val.displayName;
            this.campusShow = false;
        },
        /*分页查询*/
        areasList(){
            let params = {
                pageIndex: this.pageCurrent,
                pageSize: this.pageSize,
                status: this.status,
                orgId:this.rootId
            };
            this.$api.areaList(params).then(res => {
                if (res.success == true) {
                    //赋值分页总数
                    this.pageTotal = parseInt(res.totalDatas);
                    let array = [];
                    let list = res.data;
                    list.map((item,index)=>{
                        array.push(
                            Object.assign({
                                index:index+1
                            },item,{indexNum:'str'})
                        )
                    });
                    //赋值表格数据
                    this.tableData = array;
                } else {
                    this.$myLayer.errorLayer('失败')
                }
            })
        },
        //分页
        SizeChange(val) {
            this.pageSize = val;
            this.areasList();
        },
        CurrentChange(val) {
            this.pageCurrent = val;
            this.areasList();
        },
        /* 点击选择 filter-tree */
        orgSelect(val){
            this.rootId = val.id;
            this.areasList();
        },
        /* 表格的操作 */
        handleStop(row) {
            let _this = this;
            if(row.status != 0){
                _this.enable = 0;
            }else {
                _this.enable = 1;
            }
            let params = {
                id:row.id,
                status:_this.enable
            };
            this.$api.areaDis(params).then(res => {
                if (res.success == true) {
                    _this.areasList();
                    this.$myLayer.successLayer(res.msg)
                } else {
                    this.$myLayer.errorLayer(res.msg)
                }
            })
        },
        //编辑
        handleEdit(row) {
            let _this = this;
            //机构
            _this.orgId = row.row.orgId;
            _this.orgSelectText =row.row.orgName;
            this.formInfo.orgId = row.row.orgId;
            _this.refreshText = !_this.refreshText;
            _this.insSelect();
            //分校
            this.campusId = row.row.campusId;
            this.formInfo.campusId = row.row.campusName;
            //其他
            _this.formInfo.name = row.row.name;
            _this.formInfo.description = row.row.description;
            _this.formInfo.orderIndex = row.row.orderIndex;
            this.$myLayer.formLayer("编辑", $(".build-area-mdoal"), ["422px"],function () {
                _this.$refs["formInfo"].validate((valid) => {
                    if (valid) {
                        let ediData = {
                            id:row.row.id,
                            name: _this.formInfo.name,
                            campusId:_this.campusId,
                            orderIndex:_this.formInfo.orderIndex,
                            orgId:_this.orgId,
                            description:_this.formInfo.description
                        };
                        _this.$api.areaEdi(ediData).then(res => {
                            if (res.success == true) {
                                _this.areasList();
                                _this.$myLayer.successLayer(res.msg)
                            } else {
                                _this.$myLayer.errorLayer(res.msg)
                            }
                        })
                    } else {
                        return false;
                    }
                })

            });

        },
        /* 控制状态 */
        handleSwicthState(val) {
            this.status = val;
            this.areasList();
        },
        /* 添加 */
        handleAdd() {
            let _this = this;
            _this.orgId = '';
            _this.orgSelectText ='';
            _this.formInfo.orgId = '';
            _this.formInfo.name = '';
            _this.formInfo.description = '';
            _this.formInfo.orderIndex = '';
            _this.refreshText = !_this.refreshText;
            _this.$refs['formInfo'].resetFields();
            this.$myLayer.formLayer("新建", $(".build-area-mdoal"), ["422px"],function () {
                _this.$refs["formInfo"].validate((valid) => {
                    if (valid) {
                        let params = {
                            name: _this.formInfo.name,
                            campusId:_this.campusId,
                            orderIndex:_this.formInfo.orderIndex,
                            orgId:_this.orgId,
                            description:_this.formInfo.description
                        };
                        _this.$api.areaAdd(params).then(res => {
                            if (res.success == true) {
                                _this.areasList();
                                _this.$myLayer.successLayer(res.msg)
                            } else {
                                _this.$myLayer.errorLayer(res.msg)
                            }
                        })
                    } else {
                        return false;
                    }
                })
            });
        }
    }
};

</script>

<style lang="scss" scoped>
.area {
    padding: 18px 24px;
}
.area-content {
    margin-top: 24px;
}
.area-footer {
    display: flex;
    justify-content: space-between;
    margin-top: 24px;
}
.build-area-mdoal {
    display: none;
}
.campusId{
    position: absolute;
    top: 45px;
    width: 100%;
    background-color: #fff;
    border: 1px solid #ebeef5;
    border-radius: 4px;
    -webkit-box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    z-index: 9999999;
    overflow: auto;
    max-height: 130px;
}
</style>
